.product-card {
	>* {
		&:not(:last-child) {
			padding-bottom: 11rem;

			@media (max-width: 500px) {
				padding-bottom: 5rem;
			}
		}
	}

	&__page {
		>* {
			padding-bottom: 11rem;

			@media (max-width: 500px) {
				padding-bottom: 5rem;
			}
		}
	}

	// .product-card__inner

	&__inner {
		display: grid;
		gap: var(--gap);

		@media (max-width: 1024px) {
			gap: 3rem;
		}

		@media (min-width: 1025px) {
			grid-template-columns: auto 46.6rem;
			grid-template-areas:
				'slider content'
				'info links';
		}
	}

	// .product-card__slider

	&__slider {
		margin: 0;
		width: 100%;
		position: relative;

		@media (min-width: 1025px) {
			grid-area: slider;
		}

		@media (max-width: 1024px) {
			height: 50rem;
		}

		.swipper-wrapper {
			@media (max-width: 1024px) {
				height: 50rem;
			}
		}
	}

	&__slider,
	&__info {
		@media(max-width: 1024px) {
			width: calc(100vw - 3.2rem);
		}
	}

	&__slider-item {
		position: relative;
		//height: 48rem;

		img {
			@extend %cover;
		}
	}

	&__links {
		@media (min-width: 1025px) {
			grid-area: links;
		}
	}

	// .product-card__content

	&__content {
		@media (min-width: 1025px) {
			grid-area: content;
			display: flex;
			flex-direction: column;
		}
	}

	// .product-card__top-wrap

	&__top-wrap {
		@extend %flex-row;

		&:not(:last-child) {
			margin-bottom: 0.4rem;
		}
	}

	// .product-card__title

	&__title {
		@extend %text-25;
		font-weight: 500;
		padding-right: 2rem;

		@media (max-width: 500px) {
			font-size: 2rem;
			line-height: 3rem;
		}
	}

	// .product-card__logo

	&__logo {
		width: 7.6rem;
		height: 3.8rem;
		object-fit: contain;
	}

	// .product-card__prices-wrap

	&__prices-wrap {
		@extend %vertical-align;
		flex-wrap: wrap;
		gap: 1.6rem;
	}

	// .product-card__old-price

	&__old-price {
		@extend %text-12;
	}

	// .product-card__section

	&__section {
		p {
			@extend %text-13;

			&:not(:last-child) {
				margin-bottom: 1rem;
			}
		}

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	// .product-card__section-title

	&__section-title {
		@extend %text-16;
		font-weight: 600;
		margin-bottom: 1rem;
	}

	// .product-card__info

	&__info {
		@media (min-width: 1025px) {
			grid-area: info;
		}

		p {
			@extend %text-16;

			@media (max-width: 500px) {
				font-size: 1.4rem;
				line-height: 2.1rem;
			}
		}
	}

	&__videos {
		display: grid;
		gap: var(--gap);

		@media (max-width: 1024px) {
			gap: 3rem;
		}

		@media (min-width: 501px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__video {
		height: 26.2rem;

		@media (max-width: 767px) {
			height: 20rem;
		}

		iframe {
			@extend %cover;

			@media (max-width: 1024px) {
				width: calc(50vw - 3rem);
			}

			@media (max-width: 500px) {
				width: calc(100vw - 3.2rem);
			}
		}
	}

	// .product-card__tabs

	&__tabs {
		display: flex;
		//padding-bottom: 1.2rem;
		border-bottom: 0.1rem solid var(--line-color);
		margin-bottom: 3rem;

		@media (max-width: 1024px) {
			overflow-x: auto;
		}

		&::-webkit-scrollbar {
			width: 0;
			height: 0;
			-webkit-appearance: none;
		}
	}

	// .product-card__tab

	&__tab {
		@extend %text-16;
		white-space: nowrap;
		padding-bottom: 1.7rem;

		&:not(:last-child) {
			margin-right: 6.2rem;

			@media (max-width: 767px) {
				margin-right: 3rem;
			}
		}

		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 0%;
			height: 0.2rem;
			background-color: var(--dark);
			transition: $transition width;
		}

		&.active {
			@extend %text-20;
			font-weight: 500;
			color: var(--dark);

			&:after {
				width: 100%;
			}

			@media (any-hover: hover) {
				&:hover {
					color: var(--dark);
				}
			}
		}
	}
}

.product-card-delivery {
	// .product-card-delivery__row

	&__row {
		display: grid;
		gap: var(--gap);

		@media (max-width: 1024px) {
			gap: 3rem;
		}

		@media (min-width: 501px) {
			grid-template-columns: repeat(2, 1fr);
		}

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	// .product-card-delivery__item

	&__item {
		display: flex;
		align-items: flex-start;
	}

	// .product-card-delivery__item-img

	&__item-img {
		width: 3.5rem;
		height: 3.5rem;
		flex-shrink: 0;

		&:not(:last-child) {
			margin-right: 1rem;
		}
	}

	// .product-card-delivery__item-text

	&__item-text {
		@extend %text-12;
	}
}

.product-card-form {
	>* {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	// .product-card-form__title

	&__title {
		@extend %text-13;
	}

	&__buttons {
		display: grid;
		gap: 1.2rem;

		@media (min-width: 501px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__btn {
		font-weight: 500;
		padding: 1.25rem;
	}
}

.product-cart-btn {
	background-color: var(--red);

	@media (any-hover: hover) {
		&:hover {
			background-color: var(--red);
			box-shadow: 0 0.4rem 2rem rgba(0, 0, 0, 0.25);
		}
	}
}

.product-favorite-btn {
	svg {
		fill: var(--dark);
		transition: $transition fill;
	}

	@media (any-hover: hover) {
		&:hover {
			border-color: var(--red);
			color: var(--red);

			svg {
				fill: var(--red);
			}
		}
	}

	svg,
	span {
		&.favorite {
			display: none;
		}
	}

	&.favorite {

		svg,
		span {
			display: none;

			&.favorite {
				display: block;
			}
		}

		@media (any-hover: hover) {
			&:hover {
				color: var(--dark);
				border-color: var(--dark);

				svg.favorite {
					fill: var(--red);
				}
			}
		}
	}
}

.about-product {
	// .about-product__title

	&__title {
		@extend %text-16;
		font-weight: 600;

		&:not(:last-child) {
			margin-bottom: 0.8rem;
		}
	}

	// .about-product__list

	&__list {
		@extend %text-13;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}

		>div {
			&:not(:last-child) {
				margin-bottom: 0.4rem;
			}

			dt {
				color: var(--dark-gray);
				padding-right: 0.3rem;
			}
		}
	}
}
