.basket-total {
    @media(min-width: 1025px) {
        width: 46.5rem;
    }

    &__inner {
        border: 0.1rem solid var(--gray-c0);
        padding: 4rem;

        @media(max-width: 500px) {
            padding: 3rem 1.5rem;
        }
    }

    // .basket-total__auth

    &__auth {
        max-width: 33.3rem;

        &:not(:last-child) {
            margin-bottom: 1.7rem;
        }
    }

    &__auth-btn {
        font-weight: 600;
        transition: $transition color;

        @media (any-hover: hover) {
            &:hover {
                color: var(--green);
            }
        }
    }

    // .basket-total__promocode

    &__promocode {
        > * {
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
        }

        &:not(:last-child) {
            margin-bottom: 1.5rem;
        }
    }

    // .basket-total__list

    &__list {
        padding-bottom: 2.4rem;
        border-bottom: 0.1rem solid var(--line-color);

        &:not(:last-child) {
            margin-bottom: 2.4rem;
        }
    }

    // .basket-total__list-item

    &__list-item {
        @extend %flex-row;
        @extend %text-13;
        //color: #505661;

        &:not(:last-child) {
            margin-bottom: 0.8rem;
        }

        dd span {
            font-weight: 600;
            color: var(--red);
        }

    }

    // .basket-total__overall

    &__overall {
        @extend %flex-row;
        @extend %text-20;
        font-weight: 500;

        &:not(:last-child) {
            margin-bottom: 2.4rem;
        }
    }

    // .basket-total__btn

    &__btn {
        font-weight: 600;
        width: 100%;
        padding: 1.35rem;

        &:not(:last-child) {
            margin-bottom: 1.5rem;
        }
    }

    // .basket-total__agreement

    &__agreement {
        @extend %text-13;
        //color: #505661;

        a {
            //color: #101112;
            font-weight: 500;

            @media (any-hover: hover) {
                &:hover {
                    //color: var(--green);
                    color: var(--gray);
                }
            }
        }
    }
}

input[name=promocode] {
    text-transform: uppercase;

    &::placeholder {
        text-transform: none;
    }
}

.promocode {
    &__row {
        @extend %flex-row-center;
    }

    &__delete-notify {
        @extend %text-14;
    }
}

.promocode-notify {
    position: relative;
    padding-left: 2.2rem;

    &::before {
        content: '';
        position: absolute;
        @extend %pos-center-y;
        left: 0;
        width: 1.6rem;
        height: 1.6rem;
        @extend %bg-contain;
    }

    &--success {
        color: var(--green);

        &:before {
            background-image: url(../images/icons/green-check.svg);
        }
    }

    &--error {
        color: var(--red);

        &:before {
            background-image: url(../images/icons/error-cross.svg);
        }
    }
}
