.counter {
	@extend %flex-row-center;
	// .counter__btn

	&__btn {
		@extend %vertical-align;
		position: relative;
		width: 2rem;
		height: 2rem;
		padding: 0.5rem;

		svg {
			width: 1rem;
			height: 1rem;

			line {
				transition: $transition stroke;
			}
		}

		@media (any-hover: hover) {
			&:hover {
				svg line {
					stroke: var(--green);
				}
			}
		}
	}

	// .counter__input

	&__input {
		@extend %flex-center;
		font-family: 'Raleway', sans-serif;
		text-align: center;
		font-weight: 500;
		width: calc(6.7rem - 2rem * 2);
		padding: 0 0.5rem;
	}
}

.minus-btn {
	svg {
		height: 0.2rem;
	}
}

.plus-btn {
	svg {
		height: 1rem;
	}
}
