.order-item {
    // .order-item__header

    &__header {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        background-color: var(--gray-bg);
        padding: 2rem;

        @media(max-width: 500px) {
            padding: 1.5rem;
        }
    }

    // .order-item__header-wrap

    &__header-wrap {
        display: grid;
        gap: 1rem;

        @media(min-width: 768px) {
            display: flex;
            justify-content: space-between;
        }
    }

    // .order-item__title

    &__title {
        @extend %text-16;
        font-weight: 600;
    }

    // .order-item__price

    &__price {
        @extend %text-16;
        font-weight: 600;
    }

    // .order-item__header-buttons

    &__buttons {
        @extend %vertical-align;
        flex-wrap: wrap;
        gap: 2.5rem;

        @media(max-width: 500px) {
            gap: 1rem;
        }
    }

    // .order-item__status-label

    &__status-label {
        @extend %text-13;
        font-weight: 600;
        padding: 0.6rem 1.6rem;
        border-radius: 2.4rem;

        @media (any-hover: hover) {
            &:hover {
                background-color: var(--green);
                color: #fff;
            }
        }
    }

    // .order-item__follow-link

    &__follow-link {
        @extend %vertical-align;
        transition: $transition color;

        span {
            @extend %text-13;
            text-decoration: underline;
        }

        svg {
            flex-shrink: 0;
            margin-left: 0.4rem;
            width: 1.2rem;
            height: 1.2rem;
            fill: var(--dark);
            transition: $transition fill;
        }

        @media (any-hover: hover) {
            &:hover {
                color: var(--gray);

                svg {
                    fill: var(--gray);
                }
            }
        }
    }

    // .order-item__body

    &__body {
        padding: 2rem;
        border: 0.1rem solid #EFEFEF;
        border-top: none;

        @media(max-width: 500px) {
            padding: 1.5rem;
        }

        @media(min-width: 501px) {
            display: flex;
            justify-content: space-between;
        }
    }

    // .order-item__content

    &__content {
        @media(min-width: 501px) {
            padding-right: 3rem;
        }

        @media(max-width: 500px) {
            margin-bottom: 2rem;
        }
    }

    // .order-item__delivery-date

    &__delivery-date {
        font-weight: 600;

        &:not(:last-child) {
            margin-bottom: 1rem;
        }
    }

    // .order-item__delivery-place

    &__delivery-place {

        &:not(:last-child) {
            margin-bottom: 2.2rem;

            @media(max-width: 500px) {
                margin-bottom: 1rem;
            }
        }
    }

    // .order-item__order-details

    &__delivery-place,
    &__order-details {
        color: var(--gray);
    }

    &__order-details {
        @extend %text-13;
        text-decoration: underline;

        @media (any-hover: hover) {
            &:hover {
                color: #000;
            }
        }
    }

    // .order-item__images

    &__images {
        display: flex;
        flex-wrap: wrap;
        gap: 1.2rem;
    }

    // .order-item__img

    &__img {
        width: 7.5rem;
        height: 100%;
        flex-shrink: 0;
        object-fit: cover;
    }
}
