.order-product {
    position: relative;
    display: grid;
    grid-template-columns: 10rem 1fr;
    gap: 1.4rem 2rem;
    grid-template-areas:
        'picture title'
        'picture dl';

    &__img {
        grid-area: picture;

        img {
            @extend %cover;
        }
    }

    // .order-product__title

    &__title {
        grid-area: title;
    }

    &__list {
        grid-area: dl;
    }
}

.product-title {
    @extend %text-16;
    font-weight: 600;
}

.product-list {
    @extend %text-13;

    > div {
        &:not(:last-child) {
            margin-bottom: 0.6rem;
        }
    }

    dt {
        color: var(--gray);
        float: left;
        padding-right: 0.3rem;
    }
}
