.aside-popup {
    position: absolute;
    top: 0;
    right: -100%;
    z-index: 101;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    bottom: 0;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
    width: 51.5rem;
    background-color: #fff;
    transition: $transition all;
    @include scrollbar;

    &--basket {
        padding-bottom: 0;
    }

    &.active {
        right: 0;
        opacity: 1;
        visibility: visible;
        pointer-events: all;

        @media (max-width: 767px) {
            right: 0;
        }
    }

    @media (max-width: 767px) {
        width: 100%;
        right: -100%;
    }

    // .aside-popup__close

    &__close {
        position: absolute;
        top: 2rem;
        right: 2rem;
        z-index: 20;
        width: 3.2rem;
        height: 3.2rem;

        svg {
            fill: var(--dark-gray);
            transition: $transition fill;
        }

        @media (any-hover: hover) {
            &:hover {
                svg {
                    fill: var(--red);
                }
            }
        }
    }

    // .aside-popup__title

    &__title {
        @extend %text-25;
        font-weight: 500;

        &:not(:last-child) {
            margin-bottom: 3rem;
        }
    }

    &__back-btn {
        @extend %vertical-align;
        color: var(--gray);
        transition: $transition color;

        svg {
            flex-shrink: 0;
            width: 1rem;
            height: 1rem;
            margin-right: 1rem;
            transition: $transition fill;
        }

        &:not(:last-child) {
            margin-bottom: var(--gap);

            @media (max-width: 500px) {
                margin-bottom: 3rem;
            }
        }

        @media (any-hover: hover) {
            &:hover {
                color: var(--dark);

                svg {
                    fill: var(--dark);
                }
            }
        }
    }

    // .aside-popup__text

    &__text {
        &:not(:last-child) {
            margin-bottom: 4rem;
        }
    }

    // .aside-popup__tabs

    &__tabs {
        gap: 4rem;

        &:not(:last-child) {
            margin-bottom: 4rem;
        }
    }

    // .aside-popup__tab

    &__tab {
        padding-bottom: 0.9rem;
    }

    // .aside-popup__content

    &__content {
        @media (min-width: 768px) {
            width: 30rem;
        }
    }

    &__body {
        &:not(:last-child) {
            padding-bottom: 3rem;
        }
    }

    // .aside-popup__form

    &__form {
        &:not(:last-child) {
            margin-bottom: 4rem;
        }
    }

    &__form-inputs {
        display: grid;
        gap: 1.5rem;

        &:not(:last-child) {
            margin-bottom: 2rem;
        }
    }

    &__form-wrap {
        @extend %flex-row-center;

        &:not(:last-child) {
            margin-bottom: 3rem;
        }

        .form-agree {
            align-items: center;
        }

        .check-text {
            margin-left: 0.8rem;
        }
    }

    &__form-btn {
        font-weight: 600;
        padding: 1.3rem;
        width: 100%;
    }

    &__form-success {
        margin-top: 3rem;
    }

    // .aside-popup__link-btn

    &__link-btn {
        font-weight: 500;
        color: var(--green);
        text-align: left;
    }

    // .aside-popup__social

    &__social {
        padding-top: 3rem;
        border-top: 0.1rem solid #ccc;
    }

    // .aside-popup__social-title

    &__social-title {
        margin-bottom: 1.6rem;
        font-weight: 500;
    }

    &__social-list {
        gap: 2rem;

        svg {
            width: 4rem;
            height: 4rem;
            fill: var(--dark);
        }
    }
}
