.social {
    display: flex;
    flex-wrap: wrap;
    // .social__item

    &__item {
        flex-shrink: 0;

        svg {
            fill: var(--dark);
            transition: $transition fill;
        }

        @media (any-hover: hover) {
            &:hover {
                svg {
                    fill: var(--red);
                }
            }
        }
    }

    &__link {
        @extend %flex-center;
        width: 100%;
        height: 100%;
    }
}
