.pickup-filters {
    background-color: var(--gray-bg);
    padding: 1.4rem 2.5rem;
    cursor: default;
    // .pickup-filters__title

    &__title {
        @extend %text-12;
        font-weight: 600;
        color: var(--dark-gray);

        &:not(:last-child) {
            margin-bottom: 0.5rem;
        }
    }

    // .pickup-filters__row

    &__row {
        display: flex;
        flex-wrap: wrap;
        gap: 0.5rem;
    }

    // .pickup-filters__item

    &__item {
        display: flex;
    }

    &__item-box {
        cursor: pointer;

        .check-inner {
            @extend %vertical-align;
            height: 2.3rem;
            padding: 0 0.5rem;

            svg {
                flex-shrink: 0;
                margin-right: 0.5rem;
                width: 1.4rem;
                height: 1.4rem;
                fill: var(--dark);

                &.stroke {
                    fill: none;
                    stroke: var(--dark);
                }
            }

            .check-text {
                @extend %text-10;
                letter-spacing: 0.1em;
                font-weight: 500;
                transition: $transition color;
            }
        }

        .check-box:checked + .check-inner {
            background-color: var(--dark);

            svg {
                fill: #fff;

                &.stroke {
                    fill: none;
                    stroke: #fff;
                }
            }

            .check-text {
                color: #fff;
            }
        }
    }

    &__item-remove {
        @extend %flex-center;
        width: 2.3rem;
        height: 2.3rem;
        background-color: var(--dark);
        margin-left: 0.3rem;
        transition: $transition background-color;

        svg {
            width: 1rem;
            height: 1rem;
            fill: #fff;
        }

        @media (any-hover: hover) {
            &:hover {
                background-color: var(--red);
            }
        }
    }
}
