.price-range {

    // .price-range__item

    &__item {
        @extend %vertical-align;

        span {
            white-space: nowrap;
            margin-right: 0.6rem;
        }
    }

    // .price-range__input

    &__input {
        @extend %text-13;
        width: 9rem;
        padding: 0.45rem;
        border: 0.1rem solid var(--line-color);

        @media(max-width: 1024px) {
            width: 100%;
        }

        &::placeholder {
            color: var(--gray-c0);
        }

        &:focus {
            border-color: var(--dark);
        }
    }
}
